import React from "react";
import Button from "../ui/Button";
import { LIVE_APP_URLS } from "../../utils/constants";
import AnimatedSection from "../ui/AnimatedSection";
import Container from "../layout/Container";

const FeaturesHero = () => {
  return (
    <section className="relative bg-base-white py-20 md:py-32 overflow-hidden">
      <Container className="relative z-10 text-center">
        <AnimatedSection animationType="fadeUp" duration={600} delay={100}>
          <h1 className="text-4xl md:text-5xl lg:text-6xl font-heading font-bold mb-6 text-deep-teal leading-tight">
            Everything You Need to <br />
            <span className="text-mint-green">Cook with Confidence</span>
          </h1>
        </AnimatedSection>

        <AnimatedSection animationType="fadeUp" duration={600} delay={300}>
          <p className="text-xl md:text-2xl font-body text-charcoal mb-12 max-w-2xl mx-auto">
            From step‑by‑step guidance to meal planning—SimpliChef brings the kitchen to you.
          </p>
        </AnimatedSection>

        <AnimatedSection animationType="fadeUp" duration={600} delay={500}>
          <div className="flex justify-center mb-16">
            <Button
              href={LIVE_APP_URLS.hero}
              target="_blank"
              rel="noopener noreferrer"
              variant="accent"
              size="xl"
              className="px-8 py-4 text-xl md:text-2xl font-heading font-semibold"
            >
              Try the Beta
            </Button>
          </div>
        </AnimatedSection>

        <AnimatedSection animationType="fadeUp" duration={600} delay={700}>
          <div className="flex justify-center">
            <button
              onClick={() => {
                const featuresGrid = document.getElementById("features-grid");
                featuresGrid?.scrollIntoView({ behavior: "smooth" });
              }}
              className="text-charcoal hover:text-mint-green transition-colors"
              aria-label="Scroll to features"
            >
              <svg
                className="w-8 h-8 animate-bounce"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M19 14l-7 7m0 0l-7-7m7 7V3"
                />
              </svg>
            </button>
          </div>
        </AnimatedSection>
      </Container>
    </section>
  );
};

export default FeaturesHero; 