import React from "react";
import AnimatedSection from "../ui/AnimatedSection";

const steps = [
  {
    title: "Select a Skill or Recipe",
    icon: (
      <svg
        className="w-8 h-8"
        fill="none"
        stroke="currentColor"
        viewBox="0 0 24 24"
      >
        <path
          strokeLinecap="round"
          strokeLinejoin="round"
          strokeWidth={2}
          d="M9 5H7a2 2 0 00-2 2v12a2 2 0 002 2h10a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2"
        />
      </svg>
    ),
  },
  {
    title: "Follow the Guided Steps",
    icon: (
      <svg
        className="w-8 h-8"
        fill="none"
        stroke="currentColor"
        viewBox="0 0 24 24"
      >
        <path
          strokeLinecap="round"
          strokeLinejoin="round"
          strokeWidth={2}
          d="M13 10V3L4 14h7v7l9-11h-7z"
        />
      </svg>
    ),
  },
  {
    title: "Track Your Progress & Earn Badges",
    icon: (
      <svg
        className="w-8 h-8"
        fill="none"
        stroke="currentColor"
        viewBox="0 0 24 24"
      >
        <path
          strokeLinecap="round"
          strokeLinejoin="round"
          strokeWidth={2}
          d="M5 3v4M3 5h4M6 17v4m-2-2h4m5-16l2.286 6.857L21 12l-5.714 2.143L13 21l-2.286-6.857L5 12l5.714-2.143L13 3z"
        />
      </svg>
    ),
  },
  {
    title: "Plan & Shop with Ease",
    icon: (
      <svg
        className="w-8 h-8"
        fill="none"
        stroke="currentColor"
        viewBox="0 0 24 24"
      >
        <path
          strokeLinecap="round"
          strokeLinejoin="round"
          strokeWidth={2}
          d="M16 11V7a4 4 0 00-8 0v4M5 9h14l1 12H4L5 9z"
        />
      </svg>
    ),
  },
];

const HowItWorks = () => {
  return (
    <section className="py-20 md:py-32">
      <AnimatedSection animationType="fadeUp" duration={600} delay={100}>
        <h2 className="text-3xl md:text-4xl font-heading font-bold text-deep-teal text-center mb-12">
          How It Works
        </h2>
      </AnimatedSection>

      <div className="relative">
        <div className="absolute top-1/2 left-0 right-0 h-0.5 bg-sunshine-yellow/30 transform -translate-y-1/2 hidden md:block">
          <div className="absolute top-0 left-0 right-0 h-0.5 border-t border-dashed border-sunshine-yellow"></div>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-4 gap-8">
          {steps.map((step, index) => (
            <AnimatedSection
              key={step.title}
              animationType="fadeUp"
              duration={600}
              delay={200 * (index + 1)}
            >
              <div className="relative">
                <div className="bg-light-gray rounded-full w-20 h-20 mx-auto mb-4 flex items-center justify-center text-mint-green">
                  {step.icon}
                </div>
                <div className="text-center">
                  <div className="text-2xl font-heading font-bold text-deep-teal mb-2">
                    {index + 1}
                  </div>
                  <h3 className="text-xl font-heading font-semibold text-charcoal">
                    {step.title}
                  </h3>
                </div>
              </div>
            </AnimatedSection>
          ))}
        </div>
      </div>
    </section>
  );
};

export default HowItWorks; 