import React from "react";
import AnimatedSection from "../ui/AnimatedSection";

const comingSoonFeatures = [
  {
    title: "Voice-Activated Coach",
    icon: (
      <svg
        className="w-8 h-8"
        fill="none"
        stroke="currentColor"
        viewBox="0 0 24 24"
      >
        <path
          strokeLinecap="round"
          strokeLinejoin="round"
          strokeWidth={2}
          d="M19 11a7 7 0 01-7 7m0 0a7 7 0 01-7-7m7 7v4m0 0H8m4 0h4m-4-8a3 3 0 01-3-3V5a3 3 0 116 0v6a3 3 0 01-3 3z"
        />
      </svg>
    ),
    description: "Hands-free guidance, recipe Q&A on demand.",
  },
  {
    title: "Mood-Based Recipes",
    icon: (
      <svg
        className="w-8 h-8"
        fill="none"
        stroke="currentColor"
        viewBox="0 0 24 24"
      >
        <path
          strokeLinecap="round"
          strokeLinejoin="round"
          strokeWidth={2}
          d="M14.828 14.828a4 4 0 01-5.656 0M9 10h.01M15 10h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z"
        />
      </svg>
    ),
    description: "Choose by comfort, quick-fix or celebration.",
  },
  {
    title: "AI-Driven Personalization",
    icon: (
      <svg
        className="w-8 h-8"
        fill="none"
        stroke="currentColor"
        viewBox="0 0 24 24"
      >
        <path
          strokeLinecap="round"
          strokeLinejoin="round"
          strokeWidth={2}
          d="M13 10V3L4 14h7v7l9-11h-7z"
        />
      </svg>
    ),
    description: "Macros, allergies & taste preferences tuned just for you.",
  },
  {
    title: "Student Marketplace",
    icon: (
      <svg
        className="w-8 h-8"
        fill="none"
        stroke="currentColor"
        viewBox="0 0 24 24"
      >
        <path
          strokeLinecap="round"
          strokeLinejoin="round"
          strokeWidth={2}
          d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z"
        />
        <path
          strokeLinecap="round"
          strokeLinejoin="round"
          strokeWidth={2}
          d="M15 11a3 3 0 11-6 0 3 3 0 016 0z"
        />
      </svg>
    ),
    description: "Buy & sell home-cooked meals on campus.",
  },
];

const ComingSoon = () => {
  return (
    <section className="py-20 md:py-32">
      <AnimatedSection animationType="fadeUp" duration={600} delay={100}>
        <h2 className="text-3xl md:text-4xl font-heading font-bold text-deep-teal text-center mb-12">
          Coming Soon
        </h2>
      </AnimatedSection>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
        {comingSoonFeatures.map((feature, index) => (
          <AnimatedSection
            key={feature.title}
            animationType="fadeUp"
            duration={600}
            delay={200 * (index + 1)}
          >
            <div className="group relative bg-light-gray rounded-xl p-6 text-center hover:shadow-md transition-shadow">
              <div className="text-sunrise-orange mb-4">{feature.icon}</div>
              <h3 className="text-xl font-heading font-bold text-sunrise-orange mb-2">
                {feature.title}
              </h3>
              <p className="text-charcoal font-body">{feature.description}</p>
              <div className="absolute bottom-0 left-0 right-0 h-1 bg-sunrise-orange/30 transform scale-x-0 group-hover:scale-x-100 transition-transform origin-left"></div>
            </div>
          </AnimatedSection>
        ))}
      </div>
    </section>
  );
};

export default ComingSoon; 