import React from "react";
import Button from "../ui/Button";
import { LIVE_APP_URLS } from "../../utils/constants";
import AnimatedSection from "../ui/AnimatedSection";

const FeaturesCTA = () => {
  return (
    <section className="py-20 md:py-32">
      <AnimatedSection animationType="fadeUp" duration={600} delay={100}>
        <div className="bg-mint-green rounded-2xl p-12 text-center">
          <h2 className="text-3xl md:text-4xl font-heading font-bold text-base-white mb-6">
            Ready to Cook Better?
          </h2>
          <p className="text-xl font-body text-base-white/90 mb-8 max-w-2xl mx-auto">
            Join our beta and get full access today.
          </p>
          <Button
            href={LIVE_APP_URLS.hero}
            target="_blank"
            rel="noopener noreferrer"
            variant="accent"
            size="xl"
            className="px-8 py-4 text-xl md:text-2xl font-heading font-semibold hover:scale-105 transition-transform"
          >
            Start Cooking
          </Button>
        </div>
      </AnimatedSection>
    </section>
  );
};

export default FeaturesCTA; 