import React from "react";
import AnimatedSection from "../ui/AnimatedSection";
import cookingScreen from "../../assets/images/cooking-screen.png";
import recipeGenerator from "../../assets/images/recipe-generator.png";
import skillBranches from "../../assets/images/skill-branches.png";
import mealPlanner from "../../assets/images/meal-planner.png";
import groceryList from "../../assets/images/grocery-list.png";

const features = [
  {
    title: "Cook in Real Time",
    image: cookingScreen,
    bullets: [
      "Live step timers & visual cues",
      "Quick 'what's next?' prompts",
      "Hands-free mode (voice coming soon)",
    ],
  },
  {
    title: "Your Pantry, Your Recipes",
    image: recipeGenerator,
    bullets: [
      "Match ingredients on hand",
      "Dietary filters (vegan, halal, budget)",
      "Randomize for fun discovery",
    ],
  },
  {
    title: "Build Kitchen Mastery",
    image: skillBranches,
    bullets: [
      "Structured learning paths",
      "Unlock new techniques as you progress",
      "Track your chef level",
    ],
  },
  {
    title: "Plan Your Week",
    image: mealPlanner,
    bullets: [
      "Drag-and-drop meals into your calendar",
      "Auto-generate grocery list",
      "Sync with your dietary goals",
    ],
  },
  {
    title: "Shop Smarter",
    image: groceryList,
    bullets: [
      "One-click list from your plan",
      "Organizes by aisle",
      "Export to mobile or print",
    ],
  },
];

const FeatureGrid = () => {
  return (
    <section id="features-grid" className="py-20 md:py-32">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-12">
        {features.map((feature, index) => (
          <AnimatedSection
            key={feature.title}
            animationType="fadeUp"
            duration={600}
            delay={100 * index}
          >
            <div
              className={`flex flex-col ${
                index % 2 === 0 ? "md:flex-row" : "md:flex-row-reverse"
              } gap-8 items-center`}
            >
              <div className="w-full md:w-1/2">
                <div className="bg-light-gray rounded-xl p-4 shadow-sm">
                  <img
                    src={feature.image}
                    alt={feature.title}
                    className="w-full h-auto rounded-lg"
                  />
                </div>
              </div>
              <div className="w-full md:w-1/2">
                <h3 className="text-2xl font-heading font-bold text-deep-teal mb-4 relative inline-block">
                  {feature.title}
                  <span className="absolute bottom-0 left-0 w-full h-1 bg-mint-green/30"></span>
                </h3>
                <ul className="space-y-2">
                  {feature.bullets.map((bullet) => (
                    <li
                      key={bullet}
                      className="flex items-start text-charcoal font-body"
                    >
                      <svg
                        className="w-5 h-5 text-mint-green mr-2 mt-1 flex-shrink-0"
                        fill="none"
                        stroke="currentColor"
                        viewBox="0 0 24 24"
                      >
                        <path
                          strokeLinecap="round"
                          strokeLinejoin="round"
                          strokeWidth={2}
                          d="M5 13l4 4L19 7"
                        />
                      </svg>
                      {bullet}
                    </li>
                  ))}
                </ul>
              </div>
            </div>
          </AnimatedSection>
        ))}
      </div>
    </section>
  );
};

export default FeatureGrid; 